body, .wrapper {
  background: #f9f9f9;
  flex-flow: column;
  font-family: SF Pro Text;
  display: flex;
}

.fixed-body {
  overflow: hidden;
}

.popup-wrapper {
  z-index: 100;
  width: 100vw;
  height: 100vh;
  -webkit-backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background: rgba(25, 25, 25, .4);
  transition: all .5s ease-in-out;
  position: fixed;
}

.popup-wrapper._active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.main {
  flex: 1 0 auto;
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (max-width: 1320px) {
  .container {
    max-width: 1120px;
  }
}

@media (max-width: 992px) {
  .container {
    min-width: none;
  }
}

.complited-popup {
  color: #191919;
  background: #fff;
  border-radius: 58px;
  padding: 12px 32px;
  font-family: Inter;
  font-size: 14px;
  line-height: 18px;
  display: none;
  position: fixed;
  top: 77px;
  right: 128px;
  box-shadow: 0 2px 4px rgba(25, 25, 25, .1);
}

@media (max-width: 992px) {
  .complited-popup {
    padding: 8px 24px;
    font-size: 12px;
    line-height: 16px;
    top: 25px;
    right: calc(50% - 101.5px);
  }
}

.header {
  background-color: #fff;
}

.header--fixed {
  z-index: 99;
  width: 100%;
  position: fixed;
}

.header__header-top {
  border-bottom: 1px solid #f0f0f0;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 10px 0;
  display: flex;
}

.header__header-bottom {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 10px 0;
  display: flex;
}

.header-top__text {
  color: #191919;
  background-color: #fff;
  font-family: Inter;
  font-size: 14px;
  line-height: 18px;
}

.header-top__text span {
  font-weight: 600;
}

@media (max-width: 1320px) {
  .header-top__text {
    font-size: 12px;
    line-height: 16px;
  }
}

.header-bottom__logo {
  font-family: Inter;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
}

@media (max-width: 1320px) {
  .header-bottom__logo {
    font-size: 16px;
    line-height: 16px;
  }
}

@media (max-width: 992px) {
  .header-bottom__logo {
    font-size: 14px;
    line-height: 14px;
  }
}

.header-bottom__logo-link {
  color: #191919;
  align-items: center;
  display: flex;
}

.header-bottom__logo-svg {
  width: 32px;
  height: 32px;
  fill: #ff7010;
  margin: 0 8px 0 0;
}

@media (max-width: 1320px) {
  .header-bottom__logo-svg {
    width: 28px;
    height: 28px;
    margin: 0 5px 0 0;
  }
}

@media (max-width: 992px) {
  .header-bottom__logo-svg {
    width: 24px;
    height: 24px;
  }
}

.header-bottom__nav-list {
  flex-wrap: nowrap;
  flex: auto;
  justify-content: center;
  gap: 32px;
  padding: 10px 0;
  display: none;
  position: relative;
}

@media (max-width: 1320px) {
  .header-bottom__nav-list {
    gap: 24px;
  }
}

@media (max-width: 992px) {
  .header-bottom__nav-list {
    gap: 9px;
  }
}

@media (max-width: 767px) {
  .header-bottom__nav-list {
    max-width: 440px;
    justify-content: start;
    overflow: auto;
  }
}

.header-bottom__nav-list--show {
  display: flex;
}

.header-bottom__basket {
  background: #ff7010;
  border: 1px solid #ff7010;
  border-radius: 4px;
  transition: all .2s ease-in-out;
  display: flex;
}

.header-bottom__basket:hover {
  background: #fff;
}

@media (max-width: 767px) {
  .header-bottom__basket {
    display: none;
  }
}

.header-bottom__basket-btn {
  color: #fff;
  background: none;
  align-items: center;
  padding: 9px 16px;
  font-size: 16px;
  line-height: 22px;
  transition: all .2s ease-in-out;
  display: flex;
}

.header-bottom__basket-btn:hover {
  color: #ff7010;
}

.header-bottom__basket-btn:hover .header-bottom__basket-svg {
  fill: #ff7010;
  transition: all .2s ease-in-out;
}

@media (max-width: 1320px) {
  .header-bottom__basket-btn {
    padding: 8px 12px;
    font-size: 14px;
    line-height: 18px;
  }
}

@media (max-width: 992px) {
  .header-bottom__basket-btn {
    padding: 6px 10px;
    font-size: 12px;
  }
}

.header-bottom__basket-svg {
  width: 24px;
  height: 24px;
  fill: #fff;
  margin: 0 8px 0 0;
  transition: all .2s ease-in-out;
}

.header-bottom__basket-svg:hover {
  fill: #ff7010;
}

@media (max-width: 1320px) {
  .header-bottom__basket-svg {
    width: 20px;
    height: 20px;
  }
}

@media (max-width: 992px) {
  .header-bottom__basket-svg {
    width: 18px;
    height: 18px;
  }
}

.header-bottom__burger-menu {
  background: #fff;
  display: none;
}

@media (max-width: 767px) {
  .header-bottom__burger-menu {
    display: block;
  }
}

.header-bottom__burger-line {
  height: 2px;
  background: #191919;
  margin: 0 0 7px;
  display: block;
}

.header-bottom__burger-line:last-child {
  margin: 0;
}

.header-bottom__burger-top-line, .header-bottom__burger-middle-line {
  width: 32px;
}

.header-bottom__burger-bottom-line {
  width: 20px;
}

.nav-list__item {
  position: relative;
}

@media (max-width: 767px) {
  .nav-list__item:last-child {
    display: none;
  }
}

.nav-list__link {
  color: #191919;
  background: none;
  font-size: 16px;
  line-height: 22px;
  transition: all .2s ease-in-out;
}

.nav-list__link:hover {
  color: #ef2b51;
}

@media (max-width: 1320px) {
  .nav-list__link {
    font-size: 14px;
    line-height: 18px;
  }
}

@media (max-width: 992px) {
  .nav-list__link {
    font-size: 13px;
    line-height: 14px;
  }
}

.nav-list__drop-down-svg {
  width: 16px;
  height: 16px;
  margin: 0 0 0 4px;
  transition: all .2s ease-in-out;
  position: relative;
  top: 3px;
}

@media (max-width: 1320px) {
  .nav-list__drop-down-svg {
    width: 14px;
    height: 14px;
  }
}

@media (max-width: 992px) {
  .nav-list__drop-down-svg {
    width: 12px;
    height: 12px;
  }
}

.nav-list__drop-down {
  width: 280px;
  z-index: 5;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background: #fff;
  border: 1px solid #f0f0f0;
  border-radius: 12px;
  transition: all .2s ease-in-out;
  display: block;
  position: absolute;
  top: 40px;
  right: -100px;
  transform: translate(0, 20px);
  box-shadow: 0 2px 8px rgba(25, 25, 25, .1);
}

@media (max-width: 1320px) {
  .nav-list__drop-down {
    width: 240px;
    top: 30px;
    right: -90px;
  }
}

@media (max-width: 992px) {
  .nav-list__drop-down {
    max-width: 180px;
    top: 25px;
    right: -60px;
  }
}

.nav-list__item > ._active + .nav-list__drop-down {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translate(0);
}

.nav-list__item > ._active > .nav-list__drop-down-svg {
  transform: rotate(-180deg);
}

.drop-down__link {
  color: #191919;
  padding: 8px 20px;
  font-size: 16px;
  line-height: 22px;
  transition: all .2s ease-in-out;
  display: inline-block;
}

.drop-down__link:hover {
  color: #ef2b51;
}

@media (max-width: 1320px) {
  .drop-down__link {
    font-size: 14px;
    line-height: 18px;
  }
}

@media (max-width: 992px) {
  .drop-down__link {
    font-size: 13px;
    line-height: 16px;
  }
}

